.v-menu__content {
  border-radius: 10px;
  -webkit-box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.26);
          box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.26);
}
.v-menu__content .sidebar-filter {
    height: 30px;
    line-height: 25px;
    font-size: 12px !important;
    font-weight: 600;
    color: #495057;
}
.v-menu__content .v-responsive {
    max-height: 100px;
    border-radius: 10px;
    max-width: 50px;
    margin: 0 auto;
}
.v-menu__content .container.grid-list-xl .layout .flex {
    padding: 5px;
}
.v-menu__content .v-avatar,
  .v-menu__content .v-responsive {
    border: 3px solid #ffffff;
    -webkit-transition: all .34s;
    transition: all .34s;
}
.v-menu__content .v-avatar:not(:last-child),
    .v-menu__content .v-responsive:not(:last-child) {
      margin-right: 5px;
}
.v-menu__content .v-avatar.image-active, .v-menu__content .v-avatar.color-active,
    .v-menu__content .v-responsive.image-active,
    .v-menu__content .v-responsive.color-active {
      border-color: #00bcd4;
}
.v-menu__content .v-avatar.color-primary {
    background-color: #9c27b0;
}
.v-menu__content .v-avatar.color-info {
    background-color: #00bcd4;
}
.v-menu__content .v-avatar.color-danger {
    background-color: #f44336;
}
.v-menu__content .v-avatar.color-warning {
    background-color: #ff9800;
}
.v-menu__content .v-avatar.color-success {
    background-color: #4caf50;
}
.dropdown-menu {
  border-radius: 3px;
}
.dropdown-menu .v-list__tile {
    border-radius: 2px;
    color: #333 !important;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    font-size: 0.8125rem;
    font-weight: 400;
    margin: 0 5px;
    padding: 10px 1.5rem;
    text-transform: none;
    -webkit-transition: all 0.15s linear, color, background-color, -webkit-box-shadow 0ms;
    transition: all 0.15s linear, color, background-color, -webkit-box-shadow 0ms;
    transition: all 0.15s linear, color, background-color, box-shadow 0ms;
    transition: all 0.15s linear, color, background-color, box-shadow 0ms, -webkit-box-shadow 0ms;
    white-space: nowrap;
}
.dropdown-menu .v-list__tile .v-list__tile__title {
      -webkit-transition: none;
      transition: none;
}
.dropdown-menu .v-list__tile:hover, .dropdown-menu .v-list__tile:focus, .dropdown-menu .v-list__tile:active {
      background-color: #9c27b0 !important;
      -webkit-box-shadow: 0 14px 26px -12px rgba(156, 39, 176, 0.42), 0 4px 23px 0px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(156, 39, 176, 0.2) !important;
              box-shadow: 0 14px 26px -12px rgba(156, 39, 176, 0.42), 0 4px 23px 0px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(156, 39, 176, 0.2) !important;
}
.dropdown-menu .v-list__tile:hover,
      .dropdown-menu .v-list__tile:hover .v-list__tile__title, .dropdown-menu .v-list__tile:focus,
      .dropdown-menu .v-list__tile:focus .v-list__tile__title, .dropdown-menu .v-list__tile:active,
      .dropdown-menu .v-list__tile:active .v-list__tile__title {
        color: #ffffff !important;
}

